html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background:#F1F1F1;
}
body{
  overflow-x:hidden
}
a {
  text-decoration: none;
  color: black;
}

::-webkit-scrollbar {
  width: 10px;
  height: 5px;
  background-color:rgb(186, 186, 186);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(114, 113, 113);
  border-radius: 5px;
  height: 50px;
}