*{
    font-family: 'poppins' !important; 
 }
.navbar__navitems-item::after{
    content:'';
    position:absolute;
    width:25px;
    height:5px;
    background:#e1ab1a;
    border-radius:5px;
}
@media screen and (max-width:900px){
    .navbar__navitems{
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
    }
}